.projectCard {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 1em;
  padding: 1em;
  background-color: #fff;
  border-radius: 15px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  width: 50%;
}

.projectCard:hover {
  transform: scale(1.05);
  box-shadow: 0 16px 32px rgba(0, 0, 0, 0.3);
}

.projectImage {
  width: 50%;
  height: 200px;
  object-fit: cover;
}

.projectImage:active {
  scale: 2.5;
  z-index: 9999;
}

.projectContent {
  padding: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  gap: 0.5em;
  width: 50%;
}

.projectTitle {
  font-size: 1.8em;
  margin-bottom: 0.3em;
  color: #333;
}

.projectDescription {
  font-size: 0.8em;
  color: #666;
  text-align: justify;
}

.projectStack {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5em;
  margin-bottom: 1em;
  justify-content: center;
}

.projectSkill {
  background-color: #ff416c;
  color: #fff;
  padding: 0.3em 0.75em;
  border-radius: 20px;
  font-size: 0.9em;
  transition: background-color 0.3s ease;
}

.projectButton {
  background-color: #6a6a6a;
  color: #fff;
  padding: 0.3em 0.75em;
  border-radius: 20px;
  font-size: 0.9em;
  transition: background-color 0.3s ease;
}

.projectButton:hover {
  background-color: #cabcfa;
  color: #424242;
  padding: 0.3em 0.75em;
  border-radius: 20px;
  font-size: 0.9em;
  transition: background-color 0.3s ease;
}

/* Responsividad */
@media (max-width: 768px) {
  .projectCard {
    flex-direction: column;
    width: 100%;
  }

  .projectImage {
    width: 100%;
    height: auto;
  }

  .projectContent {
    width: 100%;
  }

  .projectTitle {
    font-size: 1.5em;
  }

  .projectDescription {
    font-size: 1em;
  }
}

@media (max-width: 480px) {

  .projectCard {
    padding: 0.5em;
  }

  .projectTitle {
    font-size: 1.2em;
  }

  .projectDescription {
    font-size: 0.8em;
  }
}